import React from 'react';

// Paginationã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãŒå—ã‘å–ã‚‹ãƒ‡ãƒ¼ã‚¿ï¼ˆpropsï¼‰ã®å‹ã‚’å®šç¾©ã—ã¾ã™ã€‚
interface PaginationProps {
  currentPage: number; // ç¾åœ¨ã®ãƒšãƒ¼ã‚¸ç•ªå·
  totalPages: number;  // å…¨ä½“ã®ãƒšãƒ¼ã‚¸æ•°
  onPageChange: (page: number) => void; // ãƒšãƒ¼ã‚¸ç•ªå·ãŒå¤‰æ›´ã•ã‚ŒãŸã¨ãã«å‘¼ã°ã‚Œã‚‹é–¢æ•°
}

/**
 * ãƒšãƒ¼ã‚¸ãƒãƒ¼ã‚·ãƒ§ãƒ³ï¼ˆãƒšãƒ¼ã‚¸é€ã‚Šï¼‰ã‚’è¡¨ç¤ºãƒ»æ“ä½œã™ã‚‹ãŸã‚ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆ
 * @param {PaginationProps} props - ã“ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãŒå—ã‘å–ã‚‹ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ã€‚
 */
const Pagination: React.FC<PaginationProps> = ({ currentPage, totalPages, onPageChange }) => {
  // å‰ã®ãƒšãƒ¼ã‚¸ã¸ç§»å‹•ã™ã‚‹å‡¦ç†
  const goToPreviousPage = () => {
    // ç¾åœ¨ã®ãƒšãƒ¼ã‚¸ãŒ1ã‚ˆã‚Šå¤§ãã„å ´åˆã®ã¿ã€ãƒšãƒ¼ã‚¸ç•ªå·ã‚’1æ¸›ã‚‰ã™
    onPageChange(Math.max(1, currentPage - 1));
  };

  // æ¬¡ã®ãƒšãƒ¼ã‚¸ã¸ç§»å‹•ã™ã‚‹å‡¦ç†
  const goToNextPage = () => {
    // ç¾åœ¨ã®ãƒšãƒ¼ã‚¸ãŒç·ãƒšãƒ¼ã‚¸æ•°ã‚ˆã‚Šå°ã•ã„å ´åˆã®ã¿ã€ãƒšãƒ¼ã‚¸ç•ªå·ã‚’1å¢—ã‚„ã™
    onPageChange(Math.min(totalPages, currentPage + 1));
  };
  
  return (
    <div className="flex flex-col sm:flex-row justify-between items-center mt-6 gap-4 sm:gap-0">
      {/* ç¾åœ¨ã®ãƒšãƒ¼ã‚¸ã¨ç·ãƒšãƒ¼ã‚¸æ•°ã‚’è¡¨ç¤º */}
      <span className="text-sm text-gray-700">
        ãƒšãƒ¼ã‚¸ <span className="font-semibold">{currentPage}</span> / <span className="font-semibold">{totalPages}</span>
      </span>
      
      {/* ãƒšãƒ¼ã‚¸ç§»å‹•ãƒœã‚¿ãƒ³ */}
      <div className="flex space-x-2">
        <button 
          onClick={goToPreviousPage} 
          disabled={currentPage === 1} // æœ€åˆã®ãƒšãƒ¼ã‚¸ã§ã¯ã€Œå‰ã¸ã€ãƒœã‚¿ãƒ³ã‚’ç„¡åŠ¹åŒ–
          className="px-4 py-2 text-sm text-gray-700 bg-white border rounded-md hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          å‰ã¸
        </button>
        <button 
          onClick={goToNextPage} 
          disabled={currentPage === totalPages} // æœ€å¾Œã®ãƒšãƒ¼ã‚¸ã§ã¯ã€Œæ¬¡ã¸ã€ãƒœã‚¿ãƒ³ã‚’ç„¡åŠ¹åŒ–
          className="px-4 py-2 text-sm text-gray-700 bg-white border rounded-md hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          æ¬¡ã¸
        </button>
      </div>
    </div>
  );
};

// ã“ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’ä»–ã®ãƒ•ã‚¡ã‚¤ãƒ«ã§ä½¿ãˆã‚‹ã‚ˆã†ã«ã‚¨ã‚¯ã‚¹ãƒãƒ¼ãƒˆã—ã¾ã™ã€‚
export default Pagination;
